@import url(https://fonts.googleapis.com/css?family=Bitter|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bitter|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bitter|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #fff8fb;
  min-height: 100vh;
  color: rgb(59, 2, 31);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #792d57;
}

h1, h3, h4 {
  font-family: 'Bitter', serif !important;
}

h2, p, h5, h6 {
  font-family: 'Open Sans', sans-serif;
}


* {
    box-sizing: border-box;
  }
  
  /* Add a gray background color with some padding */
  .blog-container {
    /* padding: 20px !important; */
    width: 80%;
    /* background: white; */
  }
  
  /* Header/Blog Title */
  .header {
    width: 100%;
    font-family: 'Bitter', serif;
    padding: 40px;
    font-size: 50px !important;
    text-align: center;
    background: rgb(255, 255, 255);
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
    margin-left: 0px !important;
    margin-right: 0px !important;
  }

  .column-container {
    padding: 15px;
  }

  .followme {
    text-align: center;
  }

  
  /* Create two unequal columns that floats next to each other */
  /* Left column */
  .leftcolumn {   
    float: left;
    width: 70%;
  }
  
  /* Right column */
  .rightcolumn {
    float: left;
    width: 30%;
    padding-left: 20px;
  }
  
  /* Fake image */
  .fakeimg {
    background-image: url("https://media0.giphy.com/media/mlvseq9yvZhba/giphy.gif");
    background-color: rgb(184, 124, 157);
    width: 100%;
    padding: 20px;
    height: 100%;
  }

  .selfimg {
    width: 100%;
    margin: auto;
  }
  
  .about-section {margin: 1rem;}

  .emojis {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
  }
  /* Add a card effect for articles */
  .card {
     background-color: white;
     padding: 20px;
     margin-top: 20px;

  }
    .card h2, h2 {
    font-family: 'Bitter', serif;
    }

    p {
        font-family: 'Open Sans', sans-serif;
        font-size: 18px;
        text-align: left;
    }

    .posts {
      text-indent: 1em;
    }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Footer */
  .footer {
    padding: 20px;
    text-align: center;
    background: #ddd;
    margin-top: 20px;
  }
  
  /* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 800px) {
    .leftcolumn, .rightcolumn {   
      width: 100%;
      padding: 0;
    }
  }


    /* Portrait */
    @media only screen 
    and (min-device-width: 320px) 
    and (max-device-width: 480px)
    and (-webkit-min-device-pixel-ratio: 2) {
  
      .blog-container {
        padding: 20px !important;
        width: 100%;
        /* background: white; */
      }
        
    }@media screen and (min-width: 1400px) {
      .blog-container {
        width: 65%;
      }
    }

.icon-logo {
    height: 15vmin;
    pointer-events: none;
    margin: 3em;
    opacity: 1;
  }

.icon-logo {
  -webkit-transition: all 1.5s;
  transition: all 1.5s;
}

.icon-hover a {
  display: block;
}

.icon-hover:hover a {
    height: 16vmin;
}
  
.social-icons {
  width: 8vmin;
  margin: auto;
}

    /* Portrait */
  @media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {

      .icon-logo {
        height: 30vmin;
        pointer-events: none;
        margin: 2em;
      }

      .social-icons {
        width: 18vmin;
        margin: auto;
      }
      
    }

.nav {
    padding: .5em 1em 0 0;
    font-family: 'Open Sans', sans-serif !important;
    background-color: #fff8fb !important;
    font-size: 12px;
    text-transform: uppercase;
}

.navigation-links {
    display: flexbox;
    flex-wrap: row wrap;
    font-size: 1.2em;
    color: #792d57 !important;
    background-color: #fff8fb !important;
}

.navigation-links:hover {
    cursor: pointer;
    color: #792d57;
    font-weight: bold;
    background-color: #fff8fb !important;
    /* text-decoration: underline !important; */
    text-shadow: 1px 1px 1px 1px;
}

.navigation-links:active {
    color: #792d57;
    background-color: #fff8fb !important;
    font-weight: bold !important;
    text-decoration: underline;
}

.navigation-links:visited {
    color: #792d57;
    background-color: #fff8fb !important;
    font-weight: bold !important;
    text-decoration: underline !important;
}

.navigation-inline {
    align-items: center;
}

    /* Portrait */
    @media only screen 
    and (min-device-width: 320px) 
    and (max-device-width: 480px)
    and (-webkit-min-device-pixel-ratio: 2) {
  

        .nav {
            display: flex;
            justify-content: space-evenly;
            padding: 0 !important;
            font-size: 10px;
            text-transform: uppercase;
        }
        
    }
body, html {
    height: 100%;
}

.container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}  

h1 {
    color: #792d57;
    font-size: 4vh; 

}

.typewriter h2 {
    color: #381428;
    font-size: 2.8vh; 
    white-space: nowrap;
    overflow: hidden;
    letter-spacing: .15em;
    /* width: 30em; */
    -webkit-animation: typing 4s steps(70, end);
            animation: typing 4s steps(70, end); 
    margin-bottom: 40px;
}

/* The typing effect */
@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}
@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #792d57 ; }
}
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #792d57 ; }
}


